import React, { useState } from 'react';
import {
  AppBar,
  Toolbar,
  Typography,
  IconButton,
  Badge,
  Button,
  Box,
  Modal,
  Typography as Text,
} from '@mui/material';
import ShoppingCartIcon from '@mui/icons-material/ShoppingCart';
import { useCart } from 'react-use-cart';
import logo from './img/logo.png';
import s from './Header.module.scss';

const modalStyle = {
  position: 'absolute',
  top: '50%',
  left: '50%',
  transform: 'translate(-50%, -50%)',
  width: '90%', //     
  maxWidth: 400, //     
  bgcolor: 'background.paper',
  boxShadow: 24,
  p: 3,
  borderRadius: '10px',
  '@media (max-width:600px)': {
    p: 2, //     
  },
};

function Header({ onCartClick }) {
  const { totalItems } = useCart();
  const [openModal, setOpenModal] = useState(null); //  : "about", "contacts",  null

  const handleOpen = type => setOpenModal(type);
  const handleClose = () => setOpenModal(null);

  return (
    <AppBar
      position="fixed"
      sx={{ backgroundColor: 'black', paddingX: { xs: 2, sm: 5 } }}
    >
      <Toolbar>
        {/*  */}
        <Typography variant="h6" component="div" sx={{ flexGrow: 1 }}>
          <img src={logo} alt="logo" className={s.logo} />
        </Typography>

        {/*   */}
        <Box sx={{ display: 'flex', alignItems: 'center', gap: 2 }}>
          <Button
            color="inherit"
            sx={{ textTransform: 'none' }}
            onClick={() => handleOpen('about')}
          >
             
          </Button>
          <Button
            color="inherit"
            sx={{ textTransform: 'none' }}
            onClick={() => handleOpen('contacts')}
          >
            
          </Button>
        </Box>

        {/*   */}
        <IconButton color="inherit" onClick={onCartClick}>
          <Badge badgeContent={totalItems} color="error">
            <ShoppingCartIcon />
          </Badge>
        </IconButton>
      </Toolbar>

      {/*   */}
      <Modal
        open={!!openModal}
        onClose={handleClose}
        aria-labelledby="modal-title"
        aria-describedby="modal-description"
      >
        <Box sx={modalStyle}>
          {openModal === 'about' && (
            <>
              <Text id="modal-title" variant="h6" component="h2">
                 
              </Text>
              <Text id="modal-description" sx={{ mt: 2 }}>
                      
                -   .   ,
                     .
              </Text>
            </>
          )}
          {openModal === 'contacts' && (
            <>
              <Text id="modal-title" variant="h6" component="h2">
                
              </Text>
              <Text id="modal-description" sx={{ mt: 2 }}>
                : +380 73 362 80 54 
                <br />
                Email: unnior2007@gmail.com 
                <br />
                : . .   2
              </Text>
            </>
          )}
          <Button
            variant="contained"
            color="primary"
            fullWidth
            sx={{ mt: 3 }}
            onClick={handleClose}
          >
            
          </Button>
        </Box>
      </Modal>
    </AppBar>
  );
}

export default Header;
