import React, { useState } from 'react';
import { Modal, Box, Typography, List, ListItem, ListItemText, Button, TextField } from '@mui/material';
import { useCart } from 'react-use-cart';
import axios from 'axios';
import Notiflix from 'notiflix';
import {CHAT, URI_API } from './tg';


//     
const modalStyle = {
  position: 'absolute',
  top: '50%',
  left: '50%',
  transform: 'translate(-50%, -50%)',
  width: { xs: '90%', sm: '80%', md: 400 }, // 90%   , 80%  ,    
  bgcolor: 'background.paper',
  boxShadow: 24,
  p: { xs: 2, sm: 4 }, //    
  borderRadius: '10px',
  maxHeight: '90vh', //     
  overflowY: 'auto', // ,    
};

function OrderModal({ open, onClose }) {
  const { items, cartTotal, emptyCart } = useCart();
  const [receiverName, setReceiverName] = useState('');
  const [phone, setPhone] = useState('+380');

  const handleOrderConfirm = () => {
    if (receiverName.trim() === '') {
      Notiflix.Notify.failure(' ');
      return;
    }
    if (phone.trim() === '+380' || phone.length !== 13) {
      Notiflix.Notify.failure('   ');
      return;
    }
    if (items.length === 0) {
      Notiflix.Notify.failure('  ');
      return;
    }

    let orderMessage = '';
    items.forEach((item) => {
      orderMessage += `<b>${item.name}</b> - ${item.quantity} ., ${item.price * item.quantity} \n`;
    });

    axios
      .post(URI_API, {
        chat_id: CHAT,
        parse_mode: 'html',
        text: `<b> </b>\n<b>':</b> ${receiverName}\n<b>:</b> ${phone}\n<b>:\n</b>${orderMessage}\n<b> :</b> ${cartTotal} `,
      })
      .then((res) => {
        Notiflix.Notify.success(' ');
        emptyCart();
        setReceiverName('');
        setPhone('+380');
        onClose();
      })
      .catch((err) => {
        Notiflix.Notify.failure('     ');
      });
  };

  return (
    <Modal open={open} onClose={onClose} aria-labelledby="modal-title" aria-describedby="modal-description">
      <Box sx={modalStyle}>
        <Typography id="modal-title" variant="h6" component="h2">
           
        </Typography>
        <List sx={{ mt: 2 }}>
          {items.map((item) => (
            <ListItem key={item.id}>
              <img src={item.image} alt={item.name} style={{ width: 50, height: 50, marginRight: 10 }} />
              <ListItemText
                primary={item.name}
                secondary={`: ${item.price} , : ${item.quantity}`}
              />
            </ListItem>
          ))}
        </List>
        <Typography variant="h6" sx={{ mt: 2 }}>
            : {cartTotal} 
        </Typography>
        <TextField
          fullWidth
          label=" "
          value={receiverName}
          onChange={(e) => setReceiverName(e.target.value)}
          sx={{ mt: 2 }}
        />
        <TextField
          fullWidth
          label=" "
          value={phone}
          onChange={(e) => setPhone(e.target.value)}
          sx={{ mt: 2 }}
        />
        <Button
          variant="contained"
          color="primary"
          fullWidth
          sx={{
            mt: 2,
            backgroundColor: '#60f742',
            '&:hover': { backgroundColor: '#358026' },
            color: '#fff',
          }}
          onClick={handleOrderConfirm}
        >
           
        </Button>
        <Button
          variant="outlined"
          color="secondary"
          fullWidth
          sx={{
            mt: 2,
            backgroundColor: '#ff5100',
            '&:hover': { backgroundColor: '#e64a00' },
            color: '#fff',
          }}
          onClick={onClose}
        >
          
        </Button>
      </Box>
    </Modal>
  );
}

export default OrderModal;
